import React, { useState, useEffect, useRef } from 'react';
import GooglePayButton from '../components/GooglePayButton';
import axios from 'axios';
import './Recargas.css'; // El nombre del archivo CSS puede mantenerse

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';
const PAYPAL_CLIENT_ID = process.env.REACT_APP_PAYPAL_CLIENT_ID;

export default function Deposita() {
  const [activeTab, setActiveTab] = useState('tarjeta');
  const [monto, setMonto] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [codigoDeposito, setCodigoDeposito] = useState('');
  const [backendStatus, setBackendStatus] = useState('checking');
  const paypalButtonRef = useRef(null);
  const depositoIdRef = useRef(null);
  // --- FUNCIONES AUXILIARES ---
  const renderButtons = () => {
    if (!window.paypal || !paypalButtonRef.current) return;
    paypalButtonRef.current.innerHTML = '';
    window.paypal.Buttons({
      style: {
        layout: 'vertical',
        color: 'gold',
        shape: 'rect',
        label: 'paypal',
        height: 45,
      },
      createOrder: async () => {
        try {
          setError('');
          setSuccess('');
          setLoading(true);
          const montoNum = parseFloat(monto);
          if (!monto || isNaN(montoNum) || !isFinite(montoNum) || montoNum <= 0) {
            setError('Debes ingresar un monto vlido');
            throw new Error('Monto invlido');
          }
          if (montoNum < 1) {
            setError('El monto mnimo es $1 USD');
            throw new Error('Monto menor al mnimo');
          }
          if (montoNum > 10000) {
            setError('El monto mximo por transaccin es $10,000 USD');
            throw new Error('Monto excedido');
          }
          const token = localStorage.getItem('token');
          if (!token) {
            setError('Debes estar autenticado para depositar');
            throw new Error('No autenticado');
          }
          const response = await axios.post(
            `${API_URL}/depositos/crear-paypal`,
            { monto: montoNum },
            { headers: { Authorization: `Bearer ${token}`, 'Content-Type': 'application/json' } }
          );
          const orderId = response.data.orderId;
          depositoIdRef.current = response.data.depositoId;
          if (!orderId || !depositoIdRef.current) {
            setError('No se pudo iniciar el pago. Intenta de nuevo.');
            throw new Error('Orden invlida');
          }
          setLoading(false);
          return orderId;
        } catch (err) {
          setLoading(false);
          setError(`Error al iniciar pago: ${err.message}`);
          throw err;
        }
      },
      onApprove: async (data, actions) => {
        try {
          setLoading(true);
          setSuccess(' Procesando pago...');
          const token = localStorage.getItem('token');
          const headers = token
            ? { Authorization: `Bearer ${token}`, 'Content-Type': 'application/json' }
            : { 'Content-Type': 'application/json' };
          const response = await axios.post(
            `${API_URL}/depositos/paypal/capturar`,
            { depositoId: depositoIdRef.current, paypalOrderId: data.orderID },
            { headers }
          );
          setSuccess(` Pago completado! Saldo actualizado: $${response.data.nuevoSaldo || 'N/A'}`);
          setMonto('');
          setTimeout(() => window.location.reload(), 3000);
        } catch (err) {
          setLoading(false);
          setError('Error al capturar el pago.');
        }
      },
      onError: (err) => {
        setError(`Error al procesar el pago: ${err.message}`);
        setLoading(false);
      },
      onCancel: (data) => {
        setError('Pago cancelado. No se realiz ningn cargo.');
        setLoading(false);
      },
      onInit: (data, actions) => {},
      onClick: (data, actions) => {
        const montoNum = parseFloat(monto);
        if (!montoNum || montoNum <= 0) {
          setError('Por favor ingresa un monto vlido');
          return actions.reject();
        }
        return actions.resolve();
      }
    }).render(paypalButtonRef.current);
  };


  // (Duplicated) const verificarBackend removed to avoid redeclaration error


  // --- HOOKS ---
  useEffect(() => {
    verificarBackend();
    verificarRetornoPayPal();
  }, []);
  useEffect(() => {
    if (window.paypal) {
      renderButtons();
      return;
    }
    const scriptId = 'paypal-js-sdk';
    if (!document.getElementById(scriptId)) {
      const script = document.createElement('script');
      script.id = scriptId;
      script.src = `https://www.paypal.com/sdk/js?client-id=${PAYPAL_CLIENT_ID}&currency=USD&intent=capture`;
      script.async = true;
      script.onload = renderButtons;
      document.body.appendChild(script);
    }
  }, [activeTab, monto]);
        

  const verificarRetornoPayPal = async () => {
    const params = new URLSearchParams(window.location.search);
    const success = params.get('success');
    const cancelled = params.get('error');
    const depositoId = params.get('depositoId'); // ID de depsito en BD

    if (cancelled === 'cancelled') {
      setError('Pago cancelado por el usuario.');
      return;
    }

    if (success === 'true' && depositoId) {
      try {
        setLoading(true);
        const authToken = localStorage.getItem('token');
        if (!authToken) {
          setError('Debes estar autenticado para completar el pago.');
          return;
        }

        const response = await axios.post(
          `${API_URL}/depositos/paypal/capturar`,
          { depositoId: depositoId },
          {
            headers: {
              Authorization: `Bearer ${authToken}`,
              'Content-Type': 'application/json',
            },
          }
        );

        setSuccess(' Pago PayPal completado. Saldo actualizado.');
        console.log(' Captura PayPal:', response.data);
      } catch (err) {
        console.error(' Error capturando PayPal:', err);
        const redirectUrl = err.response?.data?.redirectUrl;
        const action = err.response?.data?.action;
        if (action === 'REDIRECT' && redirectUrl) {
          setSuccess(' Tu pago fue rechazado. Redirigiendo a PayPal para elegir otro metodo...');
          window.location.href = redirectUrl;
          return;
        }
        setError('Error al completar el pago PayPal.');
      } finally {
        setLoading(false);
      }
    }
  };

  const verificarBackend = async () => {
    try {
      const response = await axios.get(`${API_URL}/depositos/test`);
      console.log(' Backend response:', response.data);
      setBackendStatus('ok');
    } catch (err) {
      console.error(' Backend error:', err.message);
      setBackendStatus('error');
    }
  };

  const handleCanjearCodigo = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    setLoading(true);

    try {
      if (!codigoDeposito || codigoDeposito.trim() === '') {
        setError('Ingresa un cdigo de depsito vlido');
        setLoading(false);
        return;
      }

      const token = localStorage.getItem('token');
      const response = await axios.post(
        `${API_URL}/depositos/canjear-codigo`,
        { codigo: codigoDeposito.trim() },
        { headers: { Authorization: `Bearer ${token}` } }
      );

      setSuccess(` Cdigo canjeado exitosamente! Se agregaron $${response.data.montoAgregado} USD a tu saldo`);
      setCodigoDeposito('');
      setTimeout(() => setSuccess(''), 5000);
    } catch (err) {
      const mensajeError = err.response?.data?.mensaje || 'Cdigo invlido o ya utilizado';
      setError(mensajeError);
    } finally {
      setLoading(false);
    }
  };


  return (
    <div className="depositos-container">
      {/* Header */}
      <div className="depositos-header">
        <h1> Deposita tu Saldo</h1>
        <p>Agrega fondos rpido y seguro con tu tarjeta</p>
      </div>

      {/* Tabs */}
      <div className="depositos-tabs">
        <button
          className={`tab-button ${activeTab === 'tarjeta' ? 'active' : ''}`}
          onClick={() => setActiveTab('tarjeta')}
        >
          <span className="tab-logo paypal-mark" aria-hidden="true">P</span>
          <span>Depositar con PayPal</span>
        </button>
        <button
          className={`tab-button ${activeTab === 'googlepay' ? 'active' : ''}`}
          onClick={() => setActiveTab('googlepay')}
        >
          <span className="tab-logo gpay-g" aria-hidden="true">G</span>
          <span>Depositar con Google Pay</span>
        </button>
        <button
          className={`tab-button ${activeTab === 'codigo' ? 'active' : ''}`}
          onClick={() => setActiveTab('codigo')}
        >
          <span className="tab-logo tab-emoji" aria-hidden="true"></span>
          <span>Usar Cdigo</span>
        </button>
      </div>

      {/* Mensajes de error y xito */}
      {error && (
        <div className="alert alert-error">
          <span> {error}</span>
          <button className="alert-close" onClick={() => setError('')}></button>
        </div>
      )}
      {success && (
        <div className="alert alert-success">
          <span>{success}</span>
          <button className="alert-close" onClick={() => setSuccess('')}></button>
        </div>
      )}

      {/* Estado del backend */}
      {backendStatus === 'error' && (
        <div className="alert alert-warning">
           El servidor est actualizando. Por favor intenta de nuevo en unos momentos.
        </div>
      )}

      {/* TAB: Tarjeta de Crdito */}
      {activeTab === 'tarjeta' && (
        <div className="payment-container">
          <div className="payment-card">
            <div className="card-title">
              <div className="payment-logo paypal-logo" aria-label="PayPal">
                <span className="paypal-mark">P</span>
                <span className="paypal-text">PayPal</span>
              </div>
              <h2>Pago con PayPal</h2>
              <p className="card-subtitle">Paga de forma segura con tu cuenta PayPal</p>
            </div>

            <form onSubmit={(e) => e.preventDefault()} className="payment-form">
              {/* Input de monto */}
              <div className="form-section">
                <label htmlFor="monto" className="monto-label">Cunto deseas depositar?</label>
                <div className="monto-input-group">
                  <span className="currency-prefix">USD $</span>
                  <input
                    id="monto"
                    type="number"
                    value={monto}
                    onChange={(e) => setMonto(e.target.value)}
                    placeholder="0.00"
                    step="0.01"
                    min="1"
                    max="10000"
                    required
                    className="monto-input"
                  />
                </div>
                {monto && (
                  <div className="monto-summary">
                    <p className="summary-text">
                      Depositars: <span className="summary-amount">USD ${parseFloat(monto || 0).toFixed(2)}</span>
                    </p>
                    <p className="summary-info">Sin comisiones adicionales</p>
                  </div>
                )}
              </div>

              {/* Botn de PayPal (JS SDK) */}
              <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center', marginBottom: '24px'}}>
                <div className="paypal-buttons" aria-disabled={loading} style={{flex: 1}}>
                  {loading && (
                    <div className="loading-overlay">
                      <div className="spinner"></div>
                      <p>Procesando pago...</p>
                    </div>
                  )}
                  <div ref={paypalButtonRef} />
                </div>
              </div>

              {/* Indicador de estado */}
              {loading && (
                <div className="status-indicator processing">
                  <span className="status-icon"></span>
                  <span>Procesando tu pago...</span>
                </div>
              )}
              {success && !loading && (
                <div className="status-indicator success">
                  <span className="status-icon"></span>
                  <span>{success}</span>
                </div>
              )}

              {/* Info de seguridad y mtodos */}
            </form>
          </div>
        </div>
      )}

      {/* TAB: Google Pay */}
      {activeTab === 'googlepay' && (
        <div className="payment-container">
          <div className="payment-card">
            <div className="card-title">
              <div className="payment-logo gpay-logo" aria-label="Google Pay">
                <span className="gpay-g">G</span>
                <span className="gpay-text">Google Pay</span>
              </div>
              <h2>Pago con Google Pay</h2>
              <p className="card-subtitle">Paga de forma segura con Google Pay</p>
            </div>
            <form onSubmit={(e) => e.preventDefault()} className="payment-form">
              <div className="form-section">
                <label htmlFor="monto-gpay" className="monto-label">Cunto deseas depositar?</label>
                <div className="monto-input-group">
                  <span className="currency-prefix">USD $</span>
                  <input
                    id="monto-gpay"
                    type="number"
                    value={monto}
                    onChange={(e) => setMonto(e.target.value)}
                    placeholder="0.00"
                    step="0.01"
                    min="1"
                    max="10000"
                    required
                    className="monto-input"
                  />
                </div>
                {monto && (
                  <div className="monto-summary">
                    <p className="summary-text">
                      Depositars: <span className="summary-amount">USD ${parseFloat(monto || 0).toFixed(2)}</span>
                    </p>
                    <p className="summary-info">Sin comisiones adicionales</p>
                  </div>
                )}
              </div>
              <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center', marginBottom: '24px'}}>
                <div className="gpay-buttons" style={{flex: 1}}>
                  <GooglePayButton monto={monto} />
                </div>
              </div>
            </form>
          </div>
        </div>
      )}


      {/* TAB: Cdigo */}
      {activeTab === 'codigo' && (
        <div className="payment-container">
          <div className="payment-card">
            <div className="card-title">
              <h2>Canjear Cdigo de Depsito</h2>
              <p className="card-subtitle">Tienes un cdigo? salo aqu</p>
            </div>

            <form onSubmit={handleCanjearCodigo} className="payment-form">
              <div className="form-section">
                <label htmlFor="codigo" className="codigo-label">Cdigo de Depsito</label>
                <input
                  id="codigo"
                  type="text"
                  value={codigoDeposito}
                  onChange={(e) => setCodigoDeposito(e.target.value.toUpperCase())}
                  placeholder="Ej: ABC12-XYZ34-DEF56-GHI78"
                  maxLength="30"
                  required
                  className="codigo-input"
                />
                <p className="codigo-hint">Formato tpico: XXXX-XXXX-XXXX-XXXX</p>
              </div>

              <button
                type="submit"
                className="btn-payment"
                disabled={loading || !codigoDeposito.trim()}
              >
                {loading ? (
                  <>
                    <span className="spinner"></span>
                    <span>Canjeando...</span>
                  </>
                ) : (
                  <>
                    <span></span>
                    <span>Canjear Cdigo</span>
                  </>
                )}
              </button>

              <div className="codigo-info">
                <h3> Sobre los Cdigos de Depsito</h3>
                <ul>
                  <li> Los cdigos son de un solo uso</li>
                  <li> Sin fecha de expiracin</li>
                  <li> Perfectos para regalar</li>
                  <li> Se canjean al instante</li>
                  <li> Valores variados disponibles</li>
                </ul>
              </div>
            </form>
          </div>
        </div>
      )}
      <div className="payment-info">
        <div className="info-section">
          <h3> Mtodos de Pago Aceptados</h3>
          <div className="payment-methods">
            <span className="method"> PayPal</span>
            <span className="method"> Tarjeta vinculada a PayPal</span>
            <span className="method"> Google Pay</span>
          </div>
        </div>
        <div className="info-section">
          <h3> Seguridad Garantizada</h3>
          <ul className="security-list">
            <li>Encriptacin SSL de nivel banco</li>
            <li>Procesado por PayPal y Google Pay</li>
            <li>Tu informacin nunca se almacena en nuestros servidores</li>
            <li>Garanta de reembolso si hay problemas</li>
          </ul>
        </div>
        <div className="info-section">
          <h3> Proceso Rpido</h3>
          <ul className="process-list">
            <li>1 Ingresa tu monto</li>
            <li>2 Haz clic en "Proceder a Pago"</li>
            <li>3 Completa los datos de tu tarjeta o Google Pay</li>
            <li>4 Listo! Fondos disponibles instantneamente</li>
          </ul>
        </div>
        <div className="info-limits">
          <p><strong>Lmites de Depsito:</strong></p>
          <p>Mnimo: USD $1.00 | Mximo: USD $10,000.00</p>
        </div>
      </div>
    </div>
  );
}

